<template>
  <div>
    <el-dialog
      :title="(openType==1?'新增':'编辑')+'出库单'"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :lock-scroll="true"
      width="95%"
      top="5vh"
      @close="cancel">
      <el-form ref="orderInfo" :model="orderInfo" :rules="orderFormRules"  label-width="80px">
        <el-row>
          <el-col :span="5">
            <el-form-item label="商户" prop="merchants">
              <el-select v-model="orderInfo.goodsDeliverMerchantUuid" filterable placeholder="请选择商户" @change="changeMerchants">
                <el-option
                  :disabled="item.disabled"
                  v-for="(item, index) in merchantsList"
                  :key="index"
                  :label="item.userName"
                  :value="item.userUuid"
                />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="订单类型" prop="goodsDeliverType">
                <el-select v-model="orderInfo.goodsDeliverType" @change="deliverTypeChange" placeholder="请选择出库单单类型">
                  <el-option
                    v-for="(item,index) in goodsDeliverType"
                    :key="index"
                    :label="item.label"
                    :value="item.value"
                  />
                </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="送货日期" prop="goodsDeliverDate">
              <el-date-picker
                v-model="orderInfo.goodsDeliverDate"
                class="w100p"
                align="right"
                type="date"
                placeholder="选择日期"
                format="yyyy 年 MM 月 dd 日"
                value-format="yyyy-MM-dd">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <!--<el-col :span="5">-->
            <!--<el-form-item label="手机号" prop="customerServicePhone">-->
              <!--<el-input v-model="orderInfo.customerServicePhone" placeholder="固话:区号-号码" />-->
            <!--</el-form-item>-->
          <!--</el-col>-->
        </el-row>
        <el-row>
          <el-col :span="5">
            <!--<el-form-item label="客户姓名" v-if="orderInfo.goodsDeliverType == '1'" :class="orderInfo.goodsDeliverType == '1'?'infoRequired':''" prop="customerName">-->
              <!--<el-input v-model="orderInfo.customerName" maxlength="200"/>-->
            <!--</el-form-item>-->
            <el-form-item label="客户姓名" :class="orderInfo.goodsDeliverType == '1'?'infoRequired':''" prop="customerName">
              <el-input v-model="orderInfo.customerName" maxlength="200" @blur="validateField('customerName')"/>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="客户电话" :class="orderInfo.goodsDeliverType == '1'?'infoRequired':''"  prop="customerTelephone">
              <el-input v-model="orderInfo.customerTelephone" placeholder="固话:区号-号码" @blur="validateField('customerTelephone')"/>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="客户地址" :class="orderInfo.goodsDeliverType == '1'?'infoRequired':''"  prop="customerAddress">
              <el-col class="pos-r">
                <el-input v-model="orderInfo.customerAddress" ref="address" type="textarea" :autosize="{maxRows:3}" maxlength="200" resize="none" :disabled="!orderInfo.customerLat||!orderInfo.customerLng" @blur="validateField('customerAddress')"></el-input>
                <i class="el-icon-location pos-a posBtn" @click="chooseAddress(orderInfo)"></i>
              </el-col>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="备注" prop="goodsDeliverRemark">
              <el-input v-model="orderInfo.goodsDeliverRemark"  type="textarea" :autosize="{maxRows:3}" maxlength="200" class="create_textarea" resize="none" placeholder="请输入出库单备注，最长200字符" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-form-item label="电梯" prop="customerLiftFlag">
                <el-radio v-model="orderInfo.customerLiftFlag" label="0">有</el-radio>
                <el-radio v-model="orderInfo.customerLiftFlag" label="1">无</el-radio>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="是否安装" prop="customerNeedInstall">
                <el-radio v-model="orderInfo.customerNeedInstall" label="0">是</el-radio>
                <el-radio v-model="orderInfo.customerNeedInstall" label="1">否</el-radio>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <common-table :flex="400">
          <template slot="table">
          <el-table
            ref="orderTable"
            height="100%"
            stripe
            :border="true"
            v-loading="isListLoading"
            :data="tableData">
            <el-table-column type="index" label="序号" width="50" fixed="left" header-align="center"></el-table-column>
            <el-table-column label="品牌" prop="productBrand" min-width="100"></el-table-column>
            <el-table-column label="货品名称" min-width="130" prop="productName" header-align="center"></el-table-column>
            <el-table-column label="子件" min-width="140" header-align="center">
              <template slot-scope="scope">
                <el-tooltip placement="top" effect="light" enterable v-if="scope.row.productType == '1'" :visible-arrow="false">
                  <div slot="content" >
                    <div>
                      {{getItemName(scope.row.productItems,scope.row.productTempleteItemId)}}
                    </div>
                  </div>
                  <el-select v-model="scope.row.productTempleteItemId" @change="changeItem(scope.row,scope.$index)" v-if="scope.row.productType == '1' ">
                    <el-option
                      :disabled="item.disabled"
                      v-for="(item,index) in (scope.row.productItems)"
                      :key="index"
                      :label="item.productItemName"
                      :value="item.productItemUuid">
                    </el-option>
                  </el-select>
                </el-tooltip>
              </template>
            </el-table-column>
            <el-table-column label="型号" min-width="130" prop="productModel" header-align="center"></el-table-column>
            <el-table-column  label="规格" min-width="140" prop="productStandard"></el-table-column>
            <el-table-column label="颜色" min-width="80" prop="productColor"></el-table-column>
            <el-table-column label="货品类型" prop="productType" :formatter="typeFormatter" min-width="110"></el-table-column>
            <el-table-column label="数量" min-width="100" header-align="center">
              <template slot-scope="scope">
                <el-input-number v-model="scope.row.deliverItemCount" class="productCount" controls-position="right" :min="1" :precision="0" :max="scope.row.productType=='0' || scope.row.productType=='2'?scope.row.productCount:dealItemCount(scope.row)" size="mini"></el-input-number>
              </template>
            </el-table-column>
            <el-table-column label="库存数量" width="80" header-align="center">
              <template slot-scope="scope">
                <span v-if="scope.row.productType=='1'">{{dealItemCount(scope.row)}}</span>
                <span v-if="scope.row.productType=='0' || scope.row.productType=='2'">{{scope.row.productCount}}</span>
              </template>
            </el-table-column>
            <el-table-column label="备注" min-width="100" prop="deliverItemRemark" :show-tooltip-when-overflow="true" ></el-table-column>
            <el-table-column
              width="100"
              :render-header="renderHeader"
              fixed="right">
              <template slot-scope="scope">
                <el-button
                  @click.native.prevent="deleteRow(scope.$index, scope.row)"
                  type="text"
                  rules="small">
                  删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          </template>
          </common-table>
          <!--</div>-->
        </el-row>
        <el-row style="margin-top:10px">
          <el-col :span="24" class="flex-right">
            <el-button type="primary" :loading="isSaveing" @click="save()">提交</el-button>
            <el-button @click="cancel()">取消</el-button>
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
    <g-map
      :show.sync="isMapShow"
      :addressInfo="addressInfo"
      @getAddress="getAddress"
    />
    <template-select
      :show.sync="isTemplateShow"
      :merchantsList="merchantsList"
      :merchantId="orderInfo.goodsDeliverMerchantUuid"
      @sendData="receiveData"
    />
  </div>
</template>

<script>
  import Validator from '@/utils/extendValidate/extendValidate'
  import GMap from '../../Warehouse/Components/Map'
  import CommonTable from '@/components/commonTable'
  import TemplateSelect from './TemplateSelect'
  import TemplateCreate from '@/views/Development/ProductTemplate/Components/TemplateCreate'
  import TemplateImport from '@/views/Development/ProductTemplate/Components/ImportTemplate'
  import { uploadTemplate } from '@/api/warehouse'
  import { getDeliveryTemplateList, getEntryOrderDetail, updateDeliveryOrder, deleteEntryOrder,getDeliverDetail } from '@/api/order'
  import { getAllMerchantsList, getBrandModel } from '@/api/common'
  import { setSession, getSession } from '@/utils/session'

  const InitProduct = {
    productTempleteId: '',
    productBrand: '',
    productModel: '',
    productName: '',
    productStandard: '',
    productColor: '',
    deliverItemPack: '',
    productTempleteItemId: '',
    deliverItemCount: '',
    productMain: '',
    productType: '',
    deliverItemRemark: ''
  }
  export default {
    name: 'OrderCreate',
    data () {
      return {
        isSaveing: false,
        createIndex:'',
        isCreateTemplateshow: false,
        isVisible: false,
        orderInfo: {
          goodsDeliverMerchantName: '',
          goodsDeliverMerchantUuid:'',
          goodsDeliverType: '',
          goodsDeliverDate: '',
          customerName: '',
          customerAddress: '',
          customerTelephone: '',
          warehouseName: '',
          warehouseUuid: '',
          goodsDeliverRemark: '',
          customerServicePhone: '',
          customerLiftFlag: '',
          customerNeedInstall: ''
        },
        isListLoading: false,
        goodsDeliverType: [{
          value: '1',
          label: "仓库出货"
        }, {
          value: '2',
          label: "自提"
        // },{
        //   value: '3',
        //   label: "顾客端配送安装"
        // }, {
        //   value: '4',
        //   label: "展厅上样"
        // }, {
        //   value: '5',
        //   label: "展厅提货送样品"
        // }, {
        //   value: '6',
        //   label: "物流提货送货"
        // }, {
        //   value: '7',
        //   label: "展厅调场"
        // }, {
        //   value: '8',
        //   label: "外展布场"
        // }, {
        //   value: '9',
        //   label: "外地送货"
        // }, {
        //   value: '10',
        //   label: "售后维修"
        // }, {
        //   value: '11',
        //   label: "搬家(含顾客家拆装)"
        // }, ],
        // deliverItemType: [{
        //   value: '0',
        //   label: "小件"
        // }, {
        //   value: '1',
        //   label: "套件"
        // },{
        //   value: '2',
        //   label: "单品"
        }],
        originModel: [],
        existModel: [],
        modelBrand: [],
        brandList: [],
        modelList: [],
        tableData: [],
        orderFormRules: {
          goodsDeliverMerchantUuid: [
            {required: true, message: '请选择商户', trigger: 'blur'},
          ],
          goodsDeliverType: [
            {required: true, message: '请选择订单类型', trigger: 'blur'}
          ],
          goodsDeliverDate: [
            {required: true, message: '请选择送货日期', trigger: 'blur'}
          ],
          // customerServicePhone: [
          //   {required: true, message: '请输入手机号', trigger: 'blur'},
          //   {validator: Validator.isCellAndTel, trigger: 'blur'}
          // ],
          // customerLiftFlag: [
          //   {required: true, message: '请选择是否有电梯', trigger: 'blur'}
          // ],
          customerNeedInstall: [
            {required: true, message: '请选择是否安装', trigger: 'blur'}
          ]
        },
        importVisible: false,
        isMapShow: false,
        addressInfo: {},
        fromEdit: false,
        isTemplateShow: false
      }
    },
    props: {
      show: {
        type: Boolean,
        default: false
      },
      openType: {
        type: Number,
        default: 1
      },
      warehouseList: {
        type: Array
      },
      merchantsList: {
        type: Array
      },
      selectedOrderInfo: {
        type: Object
      },
      searchParams:{
        type: Object
      },
        existOrder:{
          type:Array
        }
    },
    watch: {
      show (isShow) {
        this.orderInfo = {}
        if(this.openType === 2 && isShow){
          this.orderInfo = Object.assign({},this.selectedOrderInfo)
          // this.getTemplate().then(() => {
          //   this.getBrandModel({warehouseUuid:getSession('warehouseUuid'),merchantUuid:this.orderInfo.goodsDeliverMerchantUuid})
          //   this.tableData = [].concat(this.orderInfo.goodsDeliverItems)
          //   for (let i = 0; i < this.tableData.length; i++) {
          //     let productTempleteId = this.tableData[i].productTempleteId
          //     this.existModel.push(this.originModel)
          //     let result = Object.assign(this.tableData[i],this.originModel.find(temp => { return temp.productTempleteId === productTempleteId}))
          //     this.changeModel(result,i,false)
          //   }
          // })
          this.getDeliverDetail({deliverOrderId:this.selectedOrderInfo.goodsDeliverUuid})
        }else{
          this.tableData = []
        }
        this.isVisible = isShow
      }
    },
    methods: {
      // 获取出库单详情
      getDeliverDetail(params){
        this.isListLoading = true
        getDeliverDetail(params).then(res => {
          this.isListLoading = false
          if(!res.error){
            this.tableData = res
            this.tableData.forEach(item => {
              this.changeModel(item)
            })
          }
        })
      },
      deliverTypeChange (value) {
        this.$refs.orderInfo.clearValidate()
        if(value == 1){
          console.log(this.orderFormRules)
          this.orderFormRules = Object.assign(this.orderFormRules,{
            customerName: [
              {required: true, message: '请输入客户姓名', trigger: 'blur'}
            ],
            customerAddress: [
              {required: true, message: '请输入客户地址', trigger: 'blur'}
            ],
            customerTelephone: [
              {required: true, message: '请输入客户电话', trigger: 'blur'},
              {validator: Validator.isCellAndTel, trigger: 'blur'}
            ]}
          )
        } else {
          delete this.orderFormRules.customerName
          delete this.orderFormRules.customerTelephone
          delete this.orderFormRules.customerAddress
          this.orderFormRules = Object.assign(this.orderFormRules,{
            customerTelephone: [
              {validator: Validator.isCellAndTel, trigger: 'blur'}
            ]}
          )
        }
      },
      changeMerchants(value){
        this.tableData = []
        this.existModel = []
        this.originModel = []
        this.getBrandModel({warehouseUuid:getSession('warehouseUuid'),merchantUuid:value})
        let result = this.existOrder.find(item => {
            return item.goodsDeliverMerchantUuid == value
        })
          if(this.openType == 1 && result){
              this.forbidden = true
              this.$alert('此商户存在未审核通过的出库单，请审核通过后再新增出库单', '提示', {
                  confirmButtonText: '确定'
              });
          }else{
              this.forbidden = false
          }
      },
      // 获取品牌型号列表
      getBrandModel (params) {
        getBrandModel(params).then(res => {
          if (!res.error) {
            this.brandList = []
            this.modelList = []
            res.forEach((item,index) => {
              this.brandList.push(item.productBrand)
              item.productModel.forEach(item2 => {
                this.modelList.push(item2)
              })
            })
            this.modelBrand = res
          }
        })
      },
      // 获取模板列表model
      getTemplate () {
        return new Promise(resolve => {
          let params = {
            merchantUuid :this.orderInfo.goodsDeliverMerchantUuid,
            warehouseUuid: this.orderInfo.warehouseUuid ? this.orderInfo.warehouseUuid : getSession('warehouseUuid'),
          }
          this.isListLoading = true
          getDeliveryTemplateList(params).then(res => {
            this.isListLoading = false
            this.originModel = []
            this.originModel = res
            if(this.originModel && this.originModel.length>0){
              for (let item of this.originModel) {
                item.productTempleteId = item.productUuid
                if(item.remark) {
                  item.goodsDeliverRemark = item.remark
                }
                item.disabled = false
                delete item.descOrAsc
                delete item.orderBy
                delete item.pageNum
                delete item.pageSize
                if(item.productType == '1' && item.productItems.length){
                  item.productItems.forEach(item2 => {
                    item2.disabled = false
                    if(item.remark){
                      item2.goodsDeliverRemark = item.remark
                    }
                    item.productItemUuid = item.productItems[0].productItemUuid
                  })
                }
              }
            }
            resolve()
          })
        })
      },
      renderHeader (createElement, {}) {
        return createElement(
          'el-button', {
            'class': ['addBtn','el-button--primary'],
            domProps: {
              innerHTML: '添加货品'
            },
            on: {
              click: () => { this.addRow() },
            }
          })
      },
      addRow () {
        if(!this.orderInfo.goodsDeliverMerchantUuid){
          this.$message({
            type: 'warning',
            message: '请选择商户'
          })
          return
        }
          if(this.forbidden){
              this.$alert('此商户存在未审核通过的出库单，请审核通过后再新增出库单', '提示', {
                  confirmButtonText: '确定'
              });
              return
          }
        this.isTemplateShow = true
        this.selectIndex = this.tableData.length
        // let list = Object.assign({},InitProduct)
        // InitProduct.productBrand = ''
        // this.tableData.push(list)
        // this.existModel.push(this.originModel)
      },
      deleteRow (index, row) {
        // if(rows[index].productType == '0' || rows[index].productType == '2') {
        //   if (rows.productTempleteId !== '') {
        //     for (let i = 0; i < this.existModel.length; i++) {
        //       for (let j = 0; j < this.existModel[i].length; j++) {
        //         if (this.existModel[i][j].productTempleteId === rows[index].productTempleteId) {
        //           this.existModel[i][j].disabled = false
        //         }
        //       }
        //     }
        //   }
        // } else if (rows[index].productType == '1'){
        //   if (rows.productTempleteId !== '') {
        //     for (let i = 0; i < this.existModel.length; i++) {
        //       for (let j = 0; j < this.existModel[i].length; j++) {
        //         if (this.existModel[i][j].productType == '1') {
        //           for (let k = 0; k < this.existModel[i][j].productItems.length; k++) {
        //             if (this.existModel[i][j].productItems[k].productItemUuid === rows[index].productItemUuid) {
        //               this.existModel[i][j].productItems[k].disabled = false
        //               this.existModel[i][j].disabled = false
        //             }
        //           }
        //         }
        //       }
        //     }
        //   }
        // }
        // rows.splice(index, 1)
        // this.existModel.splice(index,1)
        if(row.productType == '1' && row.productItems && row.productItems.length){
          this.tableData.forEach(item => {
            if(item.productTempleteId == row.productTempleteId){
              item.productItems.forEach(item2 => {
                if(item2.productItemUuid == row.productTempleteItemId) {
                  item2.disabled = false
                }
              })
            }
          })
        }
        this.tableData.splice(index, 1)
      },
      cancel () {
        this.existModel = []
        this.originModel = []
        this.tableData =[]
        this.orderInfo = {}
        this.$refs.orderInfo.resetFields()
        this.$emit('update:show', false)
        this.isVisible = false
      },
      receiveData (data) {
        data.forEach(item => {
          item.deliverItemCount = 1
          if (item.productType !== '') {
            item.typeDisabled = true
          }
          if (item.productType == '1' && item.productItems && item.productItems.length) {
            console.log(this.tableData)
            let itemIds = item.productItems.map(item2 => {
              return item2.productTempleteId
            })
            let existProd = this.tableData.filter(item2 => {
              return itemIds.indexOf(item2.productTempleteId) >= 0
            })
            if(existProd && existProd.length>0){
              item.productItems.forEach(item2 => {
                let existItem = existProd.find(item3 => {
                  return (item3.productTempleteItemId == item2.productItemUuid && item3.productTempleteId == item2.productTempleteId)
                })
                if (!existItem) {
                  item.productTempleteItemId = item2.productItemUuid
                  item.productTempleteId = item2.productTempleteId
                  item.deliverItemRemark = item2.deliverItemRemark
                  item2.disabled = true
                  let prod = Object.assign({},item)
                  this.tableData.unshift(prod)
                  // this.$set(this.tableData,this.selectIndex,prod)
                  this.changeModel(prod)
                  console.log(this.tableData)
                  this.selectIndex++
                }
              })
            }else{
              item.productItems.forEach(item2 => {
                item.productTempleteItemId = item2.productItemUuid
                item.productTempleteId = item2.productTempleteId
                item.deliverItemRemark = item2.deliverItemRemark
                item2.disabled = true
                let prod = Object.assign({},item)
                // this.$set(this.tableData,this.selectIndex,prod)
                this.tableData.unshift(prod)
                this.changeModel(prod)
                console.log(this.tableData)
                this.selectIndex++
              })
            }
          } else if (item.productType != '1') {
            let itemResult = this.tableData.find(item3 => {
              return (item3.productTempleteId == item.productTempleteId)
            })
            if (!itemResult) {
              let prod = Object.assign({},item)
              this.$set(this.tableData,this.selectIndex,prod)
              this.selectIndex++
            }
          }
        })
      },
      changeBrand (value,index) {
        this.fromEdit = true
        return
        if(value !== '' ){
          InitProduct.productBrand = ''
          this.$set(this.tableData,index,InitProduct)
          this.tableData[index].productBrand = value
          this.existModel[index] = this.originModel.filter(item => {
            return item.productBrand === value
          })
          console.log(this.existModel)
        } else if (value === '') {
          this.$set(this.existModel,index,[].concat(this.originModel))
        }
      },
      changeModel (row,index,showAllItems) {
        // let newProd = Object.assign({},this.existModel[index].find(item => item.productTempleteId === value.productTempleteId))
        // newProd.deliverItemCount = value.deliverItemCount
        // newProd.deliverItemPack = (value.deliverItemPack == '' ? '1' : value.deliverItemPack)
        // if(newProd.productType == '1'){
        //   let canUsed = newProd.productItems.find(item => {
        //     return item.disabled === false
        //   })
        //   if(canUsed){
        //     newProd.productTempleteItemId = canUsed.productItemUuid
        //     newProd.productCount = canUsed.productCount
        //   }
        // }
        // this.$set(this.tableData,index,newProd)
        // this.existModel.forEach(item => {
        //   if (item && item.length) {
        //     item.forEach(item2 => {
