        //           }
        //         })
        //       }
        //     })
        //   }
        // })
        // // 将未选择的货品置位可选
        // let smallModelDisabled = this.existModel[index].filter(item => {
        //   return item.disabled === true
        // })
        // if(smallModelDisabled && smallModelDisabled.length>0){
        //   smallModelDisabled.forEach(item => {
        //     let result = this.tableData.find(item2 => {
        //       return item2.productTempleteId === item.productTempleteId
        //     })
        //     if(!result){
        //       this.existModel.forEach(item2 => {
        //         item2.forEach(item3 => {
        //           if(item3.productTempleteId === item.productTempleteId){
        //             item3.disabled = false
        //             // 套件
        //             if (item3.productType == '1') {
        //               item3.productItems.forEach(item4 => {
