        //     }
        //   })
        //   // 套件
        //   let modelDisabled = this.existModel[index].filter(item => {
        //     return item.productType == '1'
        //   })
        //   console.log(this.tableData)
        //   if (modelDisabled && modelDisabled.length) {
        //     modelDisabled.forEach(item => {
        //       let result = this.tableData.filter(item2 => {
        //         return item2.productTempleteId === item.productTempleteId
        //       })
        //       if (result && result.length) {
