        //           })
        //           if(!itemResult){
        //             this.existModel.forEach(item5 => {
        //               item5.forEach(item6 => {
        //                 if(item6.productItemUuid === item.productTempleteItemId){
        //                   item6.productItems.forEach(item7 => {
        //                     if(item7.productItemUuid === item3.productItemUuid) {
        //                       item7.disabled = false
        //                       item6.disabled = false
        //                     }
        //                   })
        //                 }
        //               })
        //             })
