        //     })
        //   }
        // }
        // this.existModel[0].forEach(item => {
        //   if(item.productType == '1'){
        //     let result = item.productItems.find(item2 => {
        //       return (item2.disabled === false && item.productTempleteId === newProd.productTempleteId)
        //     })
        //     if (result) {
        //       if (showAllItems) {
        //         let list = Object.assign({},InitProduct)
        //         InitProduct.productBrand = ''
        //         this.tableData.push(list)
        //         this.existModel.push(this.originModel)
        //         this.changeModel(value,this.tableData.length-1,true)
        //       }
        //     }
        //   }
        // })
        if(row.productType == '1' && row.productItems && row.productItems.length){
          let existItem = []
          let prodResult = this.tableData.filter(item => {
            return item.productTempleteId == row.productTempleteId
          })
          if(prodResult && prodResult.length){
            prodResult.forEach(item => {
              existItem.push(item.productTempleteItemId)
            })
          }
          this.tableData.forEach((item) => {
            if(item.productTempleteId == row.productTempleteId){
              item.productItems.forEach(item2 => {
                let result = existItem.find(item3 => {
                  return item3 == item2.productItemUuid
                })
                if(!result){
                  item2.disabled = false
                } else {
                  item2.disabled = true
                }
              })
            }
          })
        }
      },
      changeItem (value,index) {
        let result = this.tableData.filter(item => {
          return item.productTempleteId === value.productTempleteId
        })
        if (result && result.length) {
          this.existModel.forEach(item => {
            item.forEach(item2 => {
              if (item2.productType === '1') {
                item2.productItems.forEach(item3 => {
                  let itemResult = result.find(item4 => {
                    return item4.productTempleteItemId === item3.productItemUuid
                  })
                  if(!itemResult){
                    item3.disabled = false
                  }else{
                    item3.disabled = true
                  }
                })
              }
            })
          })
        }
      },
      dealItemCount (row) {
        let result =  row.productItems.find(item => {
          return item.productItemUuid == row.productTempleteItemId
        })
        if(result){
          return result.productCount
        }
      },
      dealProductName (id,list) {
        let result = list.find(item => {
          return item.productTempleteId == id
        })
        if (result) {
          return (result.productName+' '+result.productModel)
        }
      },
      dealModelName (item) {
        if (item.remark) {
          return item.productModel+'「'+item.remark+'」'
        } else {
          return item.productModel
        }
      },
      dealItemName (item) {
        let itemsName = ''
        if (item.productType == '1' && item.productItems.length) {
          itemsName = '子件:'
          item.productItems.forEach((item2,index) => {
            itemsName += item2.productItemName + (index === item.productItems.length-1 ? '' : '、')
          })
        } else if (item.productType == '0') {
          itemsName = '小件'
        } else if (item.productType == '2') {
          itemsName = '单品'
        }
        return itemsName
      },
      getModelName (list,id) {
        let result = list.find(item => {
          return item.productTempleteId == id
        })
        if(result){
          return this.dealModelName(result)
        }
      },
      getItemName (list,id) {
        let result = list.find(item => {
          return item.productItemUuid == id
        })
        if(result){
          return result.productItemName
        }
      },
      validateField (prop) {
        this.$refs.orderInfo.validateField(prop)
      },
      save () {
          if(this.openType == 1 && this.forbidden){
              this.$alert('此商户存在未审核通过的出库单，请审核通过后再新增出库单', '提示', {
                  confirmButtonText: '确定'
              });
              return
          }
        this.$refs.orderInfo.validate(isValid => {
          if (isValid) {
            if(!this.tableData.length){
              this.$message({
                message: `请补全货物信息`,
                type: 'warning'
              })
              return
            }
            for(let item of this.tableData){
              if(!item||!item.productTempleteId||item.productTempleteId === ''){
                this.$message({
                  message: `请补全货物信息`,
                  type: 'warning'
                })
                return
              }
            }
            if(this.orderInfo.customerAddress && (!this.orderInfo.customerLat || !this.orderInfo.customerLng)){
              this.$message({
                message: `请在地图上选择客户位置信息`,
                type: 'warning'
              })
              return
            }
            for(let item of this.tableData){
              if (item.deliverItemRemark && item.deliverItemRemark.length) {
                item.findStorageFlag = 1
              } else {
                item.findStorageFlag = 0
              }
            }
            this.orderInfo.goodsWarehouseUuid=this.orderInfo.warehouseUuid ? this.orderInfo.warehouseUuid : getSession('warehouseUuid')
              let params = {
              ...this.orderInfo,
              goodsDeliverItems:this.tableData
            }
            this.isSaveing = true
            updateDeliveryOrder(params).then(res => {
              this.isSaveing = false
              if (!res.error) {
                let submitText = this.openType == 1 ? '新增' : '编辑'
                this.$message({
                  message: `${submitText}成功!`,
                  type: 'success'
                })
                this.cancel()
                this.queryList()
              }
            })
          }})
      },
      uploadProd () {
        this.importVisible = true
      },
      queryList () {
        this.$emit('search',this.searchParams)
      },
      chooseAddress (data) {
        this.isMapShow = true
        this.addressInfo = {
          lat:data.customerLat,
          lng:data.customerLng,
          address:data.customerAddress}
      },
      getAddress (lat,lng,address) {
        this.orderInfo.customerLat = lat
        this.orderInfo.customerLng = lng
        this.orderInfo.customerAddress = address
        this.$refs.address.$el.children[0].focus();
        this.$refs.address.$el.children[0].blur();
        console.log(this.orderInfo)
      },
      typeFormatter (row) {
        return row.productType == '0' ? '小件' : row.productType == '1' ? '套件' : row.productType == '2' ? '单品' : ''
      }
    },
    components: {
      CommonTable,
      TemplateCreate,
      TemplateImport,
      GMap,
      TemplateSelect
    }
  }
</script>

<style lang="scss">
 .posBtn{
   right: -25px;
   top: 5px;
   font-size: 20px;
   cursor: pointer;
 }
 .addBtn{
   width: 80px;
 }

 .productCount.el-input-number--mini{
     width: 90px !important;
     .el-input__inner {
       padding-left: 5px !important;
     }
 }
  .productInfo{
    height: 100%;
  }
  .itemsName{
    color: #8492a6;
    font-size: 13px;
    max-width: 300px;
    white-space: normal;
    word-break: break-all;
    word-wrap: break-word;
  }
</style>



